import styled from 'styled-components';
import palette from '../../lib/styles/palette';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import MotionPathPlugin from 'gsap/MotionPathPlugin';
import React, { useEffect, useRef } from 'react';

const Wrapper = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  /* background-color: ${palette.gray[3]}; */
`;

const SvgContainer = styled.svg`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  overflow: visible;
  .line {
    fill: none;
    stroke: black;
    stroke-width: 0.02px;
  }

  .text {
    font-size: 0.03rem;
  }
  .scroll {
    font-size: 0.02rem;
  }
`;

const TitleBox = () => {
  gsap.registerPlugin(ScrollTrigger, MotionPathPlugin); //   
  gsap.defaults({ ease: 'none' });
  const circleRef = useRef();
  const pathRef = useRef();
  const svgRef = useRef();
  const wrapperRef = useRef();
  const textRef = useRef([]);

  // react  gsap   useEffect       
  // DOM elem   useRef 
  useEffect(() => {
    gsap.from(wrapperRef.current, {
      autoAlpha: 0,
      duration: 1,
    });

    //  
    const pulses = gsap
      .timeline({
        //      
        defaults: {
          duration: 0.2,
          opacity: 0,
          repeat: 1, // yoyo   1 
          yoyo: true, //     
        },
      })
      .to([textRef[0], textRef[1]], { repeat: 0, yoyo: false }, 0)
      .from(textRef[2], {}, 0.1) //       
      .from(textRef[3], {}, 0.3)
      .from(textRef[4], {}, 0.5)
      .from(textRef[5], {}, 0.7)
      .from(textRef[6], {}, 0.9);

    //   
    gsap
      .timeline({
        defaults: { duration: 1 },
        scrollTrigger: {
          trigger: wrapperRef.current,
          start: '0 0', //     , ( ,  )
          end: '+=4000px', //    ,   
          scrub: 1,
          // id: "first",
          // markers: true,
          pin: true, //     
          pinSpacing: true, //        ,     
        },
      })
      .to(circleRef.current, {
        // ease: "none",
        immediateRender: true, //        
        motionPath: {
          // SVG path   
          path: pathRef.current,
          align: pathRef.current,
          alignOrigin: [0.5, 0.5],
        },
      })
      .add(pulses, 0);
  });

  // SVG path    
  // SVG    SVG     (path, text, circle, rect ...)
  // -      (      )
  return (
    <Wrapper ref={wrapperRef}>
      <SvgContainer ref={svgRef} viewBox="14 0 20 10">
        <text ref={(tr) => (textRef[0] = tr)} className="text" x="23" y="12">
           
        </text>
        <text
          ref={(tr) => (textRef[1] = tr)}
          className="text scroll"
          x="23.22"
          y="12.7"
        >
           
        </text>
        <text ref={(tr) => (textRef[2] = tr)} className="text" x="15" y="1.5">
             
        </text>
        <text ref={(tr) => (textRef[3] = tr)} className="text" x="15" y="2.5">
              
        </text>
        <text ref={(tr) => (textRef[4] = tr)} className="text" x="28" y="7.5">
            
        </text>
        <text ref={(tr) => (textRef[5] = tr)} className="text" x="28" y="8.5">
              
        </text>
        <text ref={(tr) => (textRef[6] = tr)} className="text" x="21.5" y="10">
            
        </text>
        <path
          ref={pathRef}
          className="line"
          d="m 0.036 5.029
                        C 32.5 6.25 17.5 -1.25 22.5 5 C 26.25
                        10 27.5 3.75 23.75 1.25 C 20 0 32.5
                        13.75 21.25 7.5 C 18.75 5 33.75 6.25
                        26.25 3.75 C 20 2.5 15 2.5 26.25 8.75
                        C 28.75 10 21.25 1.25 26.25 2.5 C 31.25
                        5 36.25 5 49.979 4.999"
        />
        <circle ref={circleRef} r="0.3" fill={palette.red[7]} />
      </SvgContainer>
    </Wrapper>
  );
};

export default TitleBox;
