import styled from 'styled-components';
import palette from '../../lib/styles/palette';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/all';
import { useEffect, useRef } from 'react';
import React from 'react';

const Wrapper = styled.div`
  position: relative;
  display: block;
  padding: 2rem;
  justify-content: space-evenly;
  align-items: center;
  min-height: 80vh;
  margin-top: 4rem;
  margin-bottom: 4rem;
  /* background-color: ${palette.gray[1]}; */
`;

const TextList = styled.div`
  & > * {
    margin: 1.5rem;
    line-height: 200%;
  }
`;
const DetailTextWrapper = styled.div`
  justify-content: space-between;
  display: flex;
  & > * {
    width: 50%;
  }
`;

const ProfileContainer = () => {
  // TO-DO : Json 

  const introduce = [
    {
      title: '  ',
      contents:
        "    .         .\
            '  '      \
                .       .",
    },
    {
      title: '      ',
      contents:
        'html, css, javascript   SPA  React, \
        react-spring, gsap, 3D   Three.js  \
             . \
       Redux     , Typescript  \
                 \
             .',
    },
    {
      title: '    ',
      contents:
        '       CORS  .\
          CORS      .\
              CORS   ,\
            .\
                 .',
    },
  ];
  const techs = [
    'HTML, CSS, JavaScript',
    'React, Redux/toolkit, Redux-Saga',
    'Typescript',
    'Three.js, React-three-fiber/drei, GSAP',
  ];
  const subs = ['nodejs, express, koa, mongodb', 'python', 'flutter'];

  gsap.registerPlugin(ScrollTrigger);
  const nameRef = useRef();
  const subListRef = useRef();
  const skillListRef = useRef();

  const nameGsap = gsap.utils.selector(nameRef);
  const subListGsap = gsap.utils.selector(subListRef);
  const skillListGsap = gsap.utils.selector(skillListRef);
  useEffect(() => {
    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: nameRef.current,
        toggleActions: 'play none none none',
      },
    });

    tl.from(nameGsap('div'), {
      opacity: 0,
      stagger: 0.1,
    })
      .from(skillListGsap('div'), {
        y: 100,
        opacity: 0,
        stagger: 0.05,
      })
      .from(subListGsap('div'), {
        y: 100,
        opacity: 0,
        stagger: 0.05,
      });
  }, [nameGsap, skillListGsap, subListGsap]);

  return (
    <Wrapper>
      <TextList ref={nameRef} className="textArea">
        <div>
          <h1></h1>
        </div>
        <div>
          <h2>   </h2>
        </div>
        <div>
          <hr />
        </div>
        {introduce.map((pg, idx) => (
          <div key={idx}>
            <h3>{pg.title}</h3>
            <div>{pg.contents}</div>
          </div>
        ))}
      </TextList>
      <DetailTextWrapper className="responsive">
        <TextList ref={skillListRef} className="textArea">
          <div>
            <h3>  </h3>
          </div>
          {techs.map((tech) => (
            <div key={tech}>{tech}</div>
          ))}
        </TextList>
        <TextList ref={subListRef} className="textArea">
          <div>
            <h3>  </h3>
          </div>
          {subs.map((sub) => (
            <div key={sub}>{sub}</div>
          ))}
        </TextList>
      </DetailTextWrapper>
    </Wrapper>
  );
};

export default ProfileContainer;
